/* eslint-disable no-mixed-operators */
/* eslint-disable react/jsx-props-no-spreading */
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import styled from 'styled-components';
import useAccountStore from '../hooks/useAccountStore.js';
import PrimaryButton from './ui/PrimaryButton.jsx';

const InputContainer = styled.div`
  margin-block: 1.5rem;
`;

const Label = styled.label`
  display: block;
`;

const Input = styled.input`
  padding: .5rem;
  height: 60px;
  width:100%;
  margin-block: .5rem;
`;

const Error = styled.p`
  color: #FF424D;
`;

export default function SignUpForm({ onSubmit }) {
  const [isDuplicated, setIsDuplicated] = useState(false);

  const {
    register, handleSubmit, watch, formState: { errors },
  } = useForm();

  const accountStore = useAccountStore();

  const handleSubmitForm = async (data) => {
    await accountStore.requestSignUp({ ...data });

    const { errorMessage } = accountStore;
    setIsDuplicated(!!errorMessage);

    if (!errorMessage) {
      onSubmit();
    }
  };

  return (
    <form onSubmit={handleSubmit(handleSubmitForm)}>
      <InputContainer>
        <Label htmlFor="input-name"> :</Label>
        <Input
          id="input-name"
          {...register('name', {
            required: ' ',
            pattern: {
              value: /^[-]{3,7}$/,
              message: '  ',
            },
          })}
        />
        {/* ?.   ? */}
        {errors.name && <Error>{errors.name.message}</Error>
        || <p>3~7   </p>}
      </InputContainer>
      <InputContainer>
        <Label htmlFor="input-userName"> :</Label>
        <Input
          id="input-userName"
          {...register('userName', {
            required: ' ',
            pattern: {
              value: /^[a-z0-9]{4,16}$/,
              message: '  ',
            },
          })}
        />
        {isDuplicated && <p>    </p>
      || errors.userName && <Error>{errors.userName.message}</Error>
      || <p>/, 4~16 </p>}
      </InputContainer>
      <InputContainer>
        <Label htmlFor="input-password"> :</Label>
        <Input
          id="input-password"
          type="password"
          {...register('password', {
            required: ' ',
            pattern: {
              value: /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/,
              message: '  ',
            },
          })}
        />
        {errors.password && <Error>{errors.password.message}</Error>
      || <p>8  (), ,    </p>}
      </InputContainer>
      <InputContainer>
        <Label htmlFor="input-password-confirm">  :</Label>
        <Input
          id="input-password-confirm"
          type="password"
          {...register('confirm', {
            required: ' ',
            // eslint-disable-next-line consistent-return
            validate: (value) => {
              if (watch('password') !== value) {
                return '  ';
              }
            },
          })}
        />
        {errors.confirm && errors.confirm.message}
      </InputContainer>
      <PrimaryButton type="submit"></PrimaryButton>
    </form>
  );
}
